@keyframes fade-out{
    0%{
        bottom: 2.5%;
    }
    100%{
        bottom: -250%; 
    }
}
@keyframes appear{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes rise{
    0%{
        opacity: 0;
        transform: translateY(25px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}
@keyframes bar-fill{
    0%{
        left: -50%;
        transform: scaleX(0);
    }
    100%{
        left: 0;
        transform: scaleX(100%);
        background-color: var(--main-primary-color);
    }
}
@keyframes load-ring {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }