.large{ width: 1100px }
.normal{ width: 800px }

@media screen and (max-width: 1150px){
    .large{ 
        width: 1000px;
        left: 0 !important; 
        transform: translateX(0) !important;
        justify-content: center;
    }
    .normal{ width: 700px }
}
@media screen and (max-width: 800px){
    .large,
    .big,
    .normal{
        width: 100%;
    }
}