.mode-toggle{
    cursor: pointer;
    position: absolute;
    right: 5%;
    width: 51px;
    height: 31px;
    border-radius: calc(var(--main-border-radius) * 3);
    background-color: var(--main-text-color);

    .circle{
        cursor: pointer;
        display: table;
        position: absolute;
        top: 4px;
        left: 4px;
        z-index: 15;
        width: 23px;
        height: 23px;
        text-align: center;
        white-space: nowrap;
        border-radius: 50%;
        background-color: var(--main-body-bg);
        transition: transform 100ms ease;
    }
}

.mode-dark{ background-color: var(--body-color) }
.circle-dark{ transform: translateX(20px) }

@media screen and (max-width: 1150px){
    .mode-toggle{ right: 5% }
}