// @import "./lang-toggle.scss";
@import "./mode-toggle.scss";

.main-navbar{
    display: flex;
    z-index: 10;
    justify-content: center;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid var(--main-border-color);

    .links a{
        position: relative;
        margin-right: 55px;
        // padding-top: 10px;
        text-decoration: none;
        color: var(--main-text-color);
        transition: color var(--main-transition);

        &::before, &::after{
            content: "";
            position: absolute;
            transition: transform var(--main-transition);
        }
        &::before{
            left: 0;
            bottom: -10px;
            width: 100%;
            height: 2px;
            background-color: var(--main-primary-color);
            transform: scaleX(0);
        }
        &:hover{ color: var(--main-title-color) }
        &:hover::before{ transform: scaleX(1) }
    }
    .links .navbar-mobile{
        display: none;
        position: absolute;
        top: 0;
        right: 0;
        width: 55px;
        height: 70px;
        background: transparent;
    }
}

@media screen and (max-width: 800px) {
    .main-navbar{ height: 50px }

    // .lang-toggle{ display: none }
    .mode-toggle{ scale: .8 }
    
    .main-navbar .links{ 
        overflow: hidden;
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: fixed;
        z-index: 10;
        top: 0;
        width: 100%;
        height: 100%;
        transform: translateY(-100%);
        text-align: center;
        background-color: var(--main-body-bg);

        a{
            padding: 30px;
            width: 100%;
            font-size: 20px;
            font-weight: 800;

            &:hover{ color: var(--main-primary-color) !important }

            &::before, &::after{ display: none }
        }
    }


    #navbar-toggle{
        cursor: pointer;
        position: fixed;
        z-index: 15;
        left: 5%;
    }

    #navbar-toggle span, 
    #navbar-toggle span::before,
    #navbar-toggle span:after{
        cursor: pointer;
        content: "";
        position: absolute;
        width: 30px;
        height: 2px;
        z-index: 15;
        background-color: var(--main-text-color);
    }
    #navbar-toggle span::before{ top: -8px }
    #navbar-toggle span::after{ bottom: -8px }

    #navbar-toggle.active span{ 
        background-color: transparent; 
        transition: background-color 400ms ease;
    }

    #navbar-toggle.active span::before,
    #navbar-toggle.active span::after{ 
        top: 0;
        transition: transform 400ms ease;
    }
    
    #navbar-toggle.active span::before{ transform: rotate(45deg) }
    #navbar-toggle.active span::after{ transform: rotate(-45deg) }

    .links.active{ 
        transform: translateY(0);
        transition: transform 400ms ease;
    }
}