.backgroundBlur{
    height: 100%; 
    filter: blur(1px);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.image-view{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    z-index: 15;
    width: 100%;
    height: 100%;
    background-color: rgba(25, 25, 25, 0.75);
    animation: appear var(--main-transition);

    &,
    *{
        user-select: none;
        -webkit-tap-highlight-color: transparent;
    }
    .load-ring{
        display: inline-block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 50px;
        height: 50px;

        &::after{
            display: block;
            margin: 0;
            width: 50px;
            height: 50px;
            content: " ";
            border: 5px solid var(--main-primary-color);
            border-radius: 50%;
            border-color: var(--main-primary-color) transparent var(--main-primary-color) transparent;
            animation: load-ring 1s linear infinite;
        }
    }
    .close{
        cursor: pointer;
        position: absolute;
        top: 15px;
        right: 20px;
        width: 40px;
        height: 40px;
        font-size: 30px;
        color: var(--main-border-color);
    
        p{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 40px;
            color: var(--main-border-color);
            transition: color 100ms ease;
        
            &:hover{
                color: #ffffff;
            }
        }
    }
    .image{
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: auto;
        max-width: 80%;
        height: auto;
        border: 2px solid var(--main-border-color);
        border-radius: var(--main-border-radius);
    }
    .ArrowLeft,
    .ArrowRight{
        cursor: pointer;
        position: absolute;
        top: 20%;
        width: 10%;
        height: 60%;
        text-align: center;
        transition: all 100ms ease;

        .arrow{
            cursor: pointer;
            display: inline-block;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            padding: 15px;
            border: solid var(--main-border-color);
            border-width: 0 10px 10px 0;
            transition: border var(--main-transition);
        }
        .left{
            transform: rotate(135deg);
        }
        .right{
            transform: rotate(-45deg);
        }
        #leftIndex,
        #rightIndex{
            display: none;
        }
    }
    .ArrowLeft{
        left: 0;
        border-top-right-radius: 5px;
        border-bottom-right-radius: 5px;
    }
    .ArrowRight{
        right: 0;
        border-top-left-radius: 5px;
        border-bottom-left-radius: 5px;
    }
}
.pf-desc{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 80px;
    text-align: center;
    
    p{
        position: relative;
        top: 50%;
        transform: translateY(-50%);
        letter-spacing: 0.5px;
        font-size: 20px;
        font-weight: 100;
        color: #ffffff;
    }
}
@media (hover: hover) and (pointer: fine){
    .ArrowLeft,
    .ArrowRight{  
        &:hover{
            background-color: rgba(0, 0, 0, 0.5);
        }
        &:hover > .arrow{
            border: solid #ffffff;
            border-width: 0 10px 10px 0;
        }
    }
}
@media screen and (max-width: 1150px){
    .close{
        top: 5px !important;
        right: 5px !important;
    }
}