@import "./animations";
@import "./text";
@import "./helpers";
// @import "./cookie-consent";
@import "./image-view";
@import "./navbar/navbar.scss";
@import "./person";
@import "./sections";
@import "./search";
@import "./markdown";
@import "./footer";

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: -apple-system, system-ui, BlinkMacSystemFont, 
        "Segoe UI", Roboto, "Helvetica Neue", 
        Arial, sans-serif;
}
.noscroll{ overflow-y: hidden }

html, body{
    overflow-x: hidden;
    scroll-behavior: smooth;

    a:not(.main-navbar a, .opencore a){
        text-decoration: none;
        color: inherit;
    }
}
body{
    // brands

    --behance-color              : #1769FF;
    --dribbble-color             : #ea4c89;
    --facebook-color             : #3b5998;
    --flickr-color               : #ff0084;
    --foursquare-color           : #0072b1;
    --github-color               : #171516;
    --google-plus-color          : #dd4b39;
    --instagram-color            : #517fa4;
    --lastfm-color               : #d51007;
    --linkedin-color             : #007bb6;
    --pinterest-color            : #cb2027;
    --reddit-color               : #ff5700;	
    --rss-color                  : #fa9b39;
    --soundcloud-color           : #ff3300;
    --stackoverflow-color        : #fe7a15;
    --tumblr-color               : #32506d;
    --twitter-color              : #55acee;
    --vimeo-color                : #1ab7ea;
    --vine-color                 : #00bf8f;
    --youtube-color              : #bb0000;
    --xing-color                 : #006567;

    // Main page variables

    // sizes

    --main-border-radius         : 5px;

    // transitions

    --main-transition            : 250ms ease;

    // colors

    --main-body-bg               : #ffffff;
    --main-card-bg               : #f7f7f7;
    --main-title-color           : #32323c;
    --main-primary-color         : #fba400;
    --main-text-color            : #525252;
    --main-border-color          : #dcdcdc;
    background-color             : var(--main-body-bg);

    &.dark{
        --main-body-bg           : #0a0f14;
        --main-card-bg           : #0f1419;
        --main-title-color       : #c8d2dc;
        --main-primary-color     : #c8b400; //ffc800
        --main-text-color        : #8c96a0;
        --main-border-color      : #32323c;
    }
    .container{ min-height: 100vh }
    
    .wrapper{
        padding-bottom: 90px;
        margin: 0 5% 0 5%;
    }
}