.search-box{
    width: 100%;
    height: fit-content;

    input{
        width: 100%;
        height: 50px;
        font-weight: 600;
    }

    .search-results{
        position: relative;
        padding: 20px;
        padding-left: 0;
        width: calc(100% + 40px);
        list-style-type: none;

        .result{
            display: block;
            margin-left: -20px;
            padding: 20px;

            &.selected{
                background-color: var(--main-card-bg);
            }
        }

    }
}