h1, h2, h3, h4, h5, h6,
input
{
    color: var(--main-title-color);
    transition: font-size 100ms ease;
}

input{
    border: none;
    border-bottom: 1px solid var(--main-border-color);
    background-color: var(--main-body-bg);
    transition: border-bottom-color var(--main-transition); 

    &:focus{
        outline: none;
        border-bottom-color: solid var(--main-title-color);
    }

    &::placeholder{ color: var(--main-text-color) }
}

p{
    color: var(--main-text-color);
    line-height: 1.5;
    word-wrap: break-word;
    transition: font-size 100ms ease;
}

strong{ font-weight: 600 !important }
b{ font-weight: 900 !important }

h1{ font-size: 31px !important }
h2, input{ font-size: 27px !important }
h3{ font-size: 24px !important }
h4{ font-size: 21px !important }
h5, p{ font-size: 20px !important }

code{ font-size: 19px !important }

label,
span{ 
    line-height: 25px;
    font-size: 15px;
    font-weight: 400;
    color: var(--main-text-color);
    transition: font-size 100ms ease;
}

label{ cursor: auto }

@media screen and (max-width: 1150px){
    h1{ font-size: 29px !important }
    h2, input{ font-size: 25px !important }
    h3{ font-size: 22px !important }
    h4{ font-size: 19px !important }
    h5, p { font-size: 18px !important }

    code{ font-size: 17px !important }
}
@media screen and (max-width: 800px){
    h1{ font-size: 27px !important }
    h2, input{ font-size: 23px !important }
    h3{ font-size: 20px !important }
    h4{ font-size: 17px !important }
    h5, p { font-size: 16px !important }

    code{ font-size: 15px !important }

    label, span{ font-size: 14px !important }
}
@media screen and (max-width: 400px){
    h1{ font-size: 26px !important }
    h2, input{ font-size: 22px !important }
    h3{ font-size: 19px !important }
    h4{ font-size: 16px !important }
    h5, p { font-size: 15px !important }

    code{ font-size: 14px !important }

    label, span{ font-size: 13px !important }
}