.person{
    padding-top: 50px;
    animation: appear 500ms ease;

    img{
        display: block;
        position: relative;
        width: 200px;
        height: 200px;
        border: 1px solid var(--main-border-color);
        border-radius: 50%;
    }
    h3{
        position: relative;
        top: 15px;
        left: 0;
        letter-spacing: .5px;
        line-height: 35px;
        font-weight: 500;
    }
    p{
        position: relative;
        left: 0;
        top: 5px;
        line-height: 50px;
        letter-spacing: 0.5px;
        font-weight: 300;
    }
    .follow-button{
        display: none;
        cursor: pointer;
        position: absolute;
        top: 55px;
        right: 0;
        padding: 8px;
        border: 1px solid var(--main-border-color);
        border-radius: var(--main-border-radius);
        font-size: 15px;
        color: var(--main-text-color);

        &:active{
            border-color: var(--main-title-color);
            background-color: var(--main-title-color);
            color: var(--main-body-bg);
        }
    }
    .socials ul{
        list-style-type: none;

        li{
            display: flex;
            margin-top: 25px;
            color: var(--main-text-color);

            a:hover{ text-decoration: underline }

            i{
                margin-right: 8px;
                font-size: 18px;
                color: var(--main-title-color);
            }
        }
    }
}

@media screen and (max-width: 800px){
    .person{
        position: relative;
        left: 0;

        img{
            width: 50px;
            height: 50px;
        }
        h3{
            position: absolute;
            top: 45px;
            left: 70px;
            letter-spacing: 0;
        }
        p{
            position: absolute;
            top: 65px;
            left: 70px;
            margin: 0;
        }
        .follow-button{ display: block }

        .socials{
            display: none;
            position: absolute;
            z-index: 5;
            right: 0;
            margin-top: 15px;
            padding: 20px;
            padding-top: 0;
            border-radius: var(--main-border-radius);
            background-color: var(--main-card-bg);
            box-shadow: 0 2px 5px 5px rgba(0,0,0,.05), 0 5px 10px 5px rgba(0,0,0,.05);

            &.active{ display: block }

            &::after{
                content: "\25b2";
                position: absolute;
                z-index: -1;
                top: -30px;
                right: 15px;
                font-size: 35px;
                color: var(--main-card-bg);
            }
        }
    }
}