.load-ring{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50px;
    height: 50px;

    &::after{
        display: block;
        margin: 0;
        width: 50px;
        height: 50px;
        content: " ";
        border: 5px solid var(--main-primary-color);
        border-radius: 50%;
        border-color: var(--main-primary-color) transparent var(--main-primary-color) transparent;
        animation: load-ring 1s linear infinite;
    }
}
.markdown-container{
    position: relative;
    left: 50%;
    transform: translateX(-50%);
    animation: appear 500ms ease;
}
.post{
    padding-top: 50px;

    a{
        color: var(--main-primary-color) !important;
    }
}
.sharing{
    margin-top: 100px;

    .share-buttons{
        display: flex;
        flex-direction: row;
        margin: 5px -5px;

        a{
            padding: 5px;

            i{ 
                padding: 10px;
                border-radius: 50%;
                transition: opacity var(--main-transition);

                &.fa-twitter{    
                    border: 1px solid var(--twitter-color);
                    color: var(--twitter-color);
                }
                &.fa-reddit{    
                    border: 1px solid var(--reddit-color);
                    color: var(--reddit-color);
                }
                &:hover{
                    opacity: .75;
                }
            }
        }
        
    }
}
.markdown{
    pre > div{
        background-color: var(--main-card-bg) !important;
    }
    a{ 
        color: var(--behance-color) !important;

        &:hover { text-decoration: underline }
    }
    h1, h2, h3, p{ 
        padding-top: 25px;
    }
    img{ 
        max-width: 100%;
    }
}

