.main-footer{
    overflow: hidden;
    clear: both;
    margin-top: 50px;
    padding: 20px;
    padding-left: 5%;
    width: 100%;
    height: 90px;
    border-top: 1px solid var(--main-border-color);
    background-color: var(--main-card-bg);

    a:hover{ text-decoration: underline }

    span li { list-style-type: none }
    span li strong { text-transform: uppercase }
}

@media screen and (max-width: 800px){
    .main-footer{
        height: 150px;
        text-align: center;

        span li strong{ line-height: 50px }
        span li span{ display: block }
    }
}