.section-container{
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: space-around;
    gap: 100px;
    left: 50%;
    transform: translateX(-50%);
    
    .section-center{
        width: 100%;
    }
}

.button{
    cursor: pointer;
    display: inline-block;
    margin-top: 40px;
    padding: 15px;
    width: fit-content;
    border: 1px solid var(--main-border-color);
    border-radius: var(--main-border-radius);
    color: var(--main-text-color);

    &:hover{
        border-color: var(--main-title-color);
        background-color: var(--main-title-color);
        transition: all var(--main-transition);
    }
    &:hover label{
        color: var(--main-body-bg) !important;
        transition: color var(--main-transition);
    }
}
.section{
    position: relative;
    padding-top: 50px;
    width: 100%;
    animation: appear 500ms ease;
}
.section-title{
    padding-bottom: 25px;
    
    p{ margin-top: 15px }
}
.section-content{
    &.grid{
        display: flex;
        flex-direction: column;
        
        .pinned{ width: calc(100% + 40px) }

        .row{
            display: flex;
            flex-shrink: auto;
            flex-wrap: wrap;
            column-gap: 40px;
            margin-top: 20px;

            & > * { flex: 1 }
            .post{
                width: calc(100% + 40px);
                height: 100%;
            }
        }
    } 
    p{ width: 65% }

    img{
        cursor: pointer;
        position: relative;
        margin-right: 50px;
        margin-top: 50px;
        margin-bottom: 50px;
        width: 100px;
        height: 100px;
        user-select: none;
        -webkit-tap-highlight-color: transparent;
        transition: all 100ms ease;
    }
    
    .post{
        position: relative;
        margin-top: 0;
        margin-left: -20px;
        padding: 20px;
        width: calc(100% + 40px);
        transition: all var(--main-transition);

        details{
            padding: 10px 0 10px 0;
            border-bottom: 1px solid var(--main-border-color);
        }
        details > summary{ list-style: none !important }
        details > summary:first-of-type{ list-style-type: none !important }
        
        details > summary::marker,
        details > summary::-webkit-details-marker{ display: none !important }

        p{ margin-top: 10px }
        
        .arrow{
            cursor: pointer;
            display: inline-block;
            position: absolute;
            top: 0;
            right: 0;
            margin-right: 5px;
            padding: 7.5px;
            border: solid var(--main-primary-color);
            border-width: 0 2.5px 2.5px 0;
            transition: all var(--main-transition);

            &.right{
                position: relative;
                top: 0;
                left: 5px;
                margin: 0;
                padding: 5px;
                scale: 0;
                transform: rotate(-45deg);
            }
            &.down, 
            &.up{
                border: solid var(--main-text-color);
                border-width: 0 2.5px 2.5px 0;
            }
            &.down{ transform: rotate(45deg) }
            &.up{
                top: 10px;
                transform: rotate(225deg); 
            }
        }

        &:hover{ background-color: var(--main-card-bg) }
        &:hover:not(.summary) .arrow{ scale: 1 }
    }
    .post.summary{
        margin: 0;
        padding: 0;
        width: 100%;

        &:hover{ background-color: transparent }

        .post-header{
            cursor: pointer;
            display: block;
            margin-top: 20px;

            h2, label{ transition: color var(--main-transition); }

            &:hover h2{ color: var(--main-primary-color) }
            &:hover label{ color: var(--main-title-color) }
            &:hover .arrow.down,
            &:hover .arrow.up{
                border: solid var(--main-title-color);
                border-width: 0 2.5px 2.5px 0;
            }
        }
    }
}
