//         left: 65%;
//     }
// }
@media screen and (max-width: 800px){
    .section-container{
        flex-direction: column;
        gap: 0;
        left: 0;
        transform: unset;

        .section-left,
        .section-right{
            width: 100%;
        }
    }
    .section{ width: 100% }
    .section-content{
        &.grid .row { column-gap: 100% }

        p{ width: 100% }
        .post{

            h2{ top: 20px }
        }
    }
}